import { Component, OnInit } from '@angular/core';
import * as $ from 'jquery';

@Component({
  selector: 'app-video-call',
  templateUrl: './video-call.component.html',
  styleUrls: ['./video-call.component.scss']
})
export class VideoCallComponent implements OnInit {

  constructor() { }

  ngOnInit(): void {
    $(document).ready(function(){    
    let nowStart = new Date().getTime();
    let countDownDate = new Date(nowStart +3000000).getTime();
    let x = setInterval(function () {
        var now = new Date().getTime();
        var distance = countDownDate - now;
        var hours = Math.floor((distance % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60));
        var minutes = Math.floor((distance % (1000 * 60 * 60)) / (1000 * 60));
        var seconds = Math.floor((distance % (1000 * 60)) / 1000);
        $("#hour").text(hours);
        $("#minute").text(minutes);
        $("#second").text(seconds);
        if (distance < 0) {
            clearInterval(x);
        }
    }, 1000);
    $(".phone-slash").click(function(){
      countDownDate = new Date().getTime();
    })
    $("#mainPage").css({marginTop :  0 +"px" });
    $("main").css({marginTop : 0 +"px" });

    $("#open-chat").click(function(){
      $(".popup-chat").css({display:"flex"});

    })
    $(document).mouseup(function (e)
{

var container = $(".container-chat");

if (!container.is(e.target) 
    && container.has(e.target).length === 0)
{
    $(".popup-chat").hide();
}
});
})
  }

}
